/*
em - от текущего шоифта
% - от родительской ширины
rem - от стиля html
vw – 1% ширины окна
vh – 1% высоты окна
vmin – наименьшее из (vw, vh), в IE9 обозначается vm
vmax – наибольшее из (vw, vh)
*/

*, *::before, *::after {
	box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
    outline: none !important;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
    display: block;
}

a {
    text-decoration: none;
    color: inherit;
    &:hover{
        text-decoration: none;
    }
}

a:focus{
    text-decoration:none;
}

dl, ol, ul {
    // margin-top: 0;
    // margin-bottom: 0;
    margin: 0;
    padding: 0;

    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0;
}

img {
    border: none;
    // display: block;
}

.clear {
    clear: both;
    width: 100%;
}

@font-face {
	font-family: 'Museo Sans Cyrl 700';
	src: url('../fonts/MuseoSansCyrl-700.eot');
	src: local('MuseoSansCyrl-700'),
		url('../fonts/') format('embedded-opentype'),
		url('../fonts/MuseoSansCyrl-700.woff') format('woff'),
		url('../fonts/MuseoSansCyrl-700.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

html, body {
	font-size: 62.5%; // 1rem = 10px
	font-family: 'Roboto', sans-serif;
	font-weight: normal;
	color: #fff;
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
}

img {
	width: 100%;
	height: 100%;
}

.container {
	width: 113.5rem;
	margin: 0 auto;
}

@media (max-width: 108em) {
    html {
        font-size: 10px;
    }
}

@media (max-width: 105em) {
    html {
        font-size: 10px;
    }
}

@media (max-width: 91.5em) {
    html {
        font-size: 9.8px;
    }
}

@media (max-width: 91em) {
    html {
        font-size: 9.6px;
    }
}

@media (max-width: 90em) {
    html {
        font-size: 9.4px;
    }
}

@media (max-width: 80em) {
    html {
        font-size: 9px
    }
}

@media (max-width: 72em) {
    html {
        font-size: 8px;
    }
}

@media (max-width: 64em) {
    html {
        font-size: 7.8px;
    }
}

.header {
	position: absolute;
	padding-bottom: 6.4rem;
	left: 50%;
	top: 6.4rem;
	transform: translate(-50%, 0);

	&__menu {
		display: flex;
		align-items: center;

		& .container {
			display: flex;
			align-items: center;
		}

		&-logo {
			width: 7.9rem;
			height: 2.9rem;
			margin-right: 3.9rem;
		}

		&-nav {
			margin-right: 70rem;
		}
		
		&-nav a {
			color: #fff;
			text-decoration: none;
			font-size: 1.4rem;
			line-height: 114%;
		}
		
		&-nav a:not(:last-child) {
			margin-right: 2.4rem;
		}

		&-swicher {
			display: flex;
			font-size: 1.4rem;
			line-height: 114%;
	
			@media screen and (min-width: 769px) {
				display: none;
			}
		}

		&-social {
			display: flex;
	
			&-icons {
				width: 1.7rem;
				height: 1.6rem;
	
				&:not(:last-child) {
					margin-right: 2rem;
				}
			}
		}
	}
}

.main {
	min-height: calc(100vh - 6rem);
	padding-top: 18.7rem;
	padding-bottom: 10.4rem;
	background: url(../images/background.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position-x: center;
	background-position-y: top;

	&__menu-burger {
		display: none;
	}

	&__rating {

		&-title {
			font-size: 1.4rem;
			line-height: 114%;
			margin-bottom: 1.6rem;
		}

		&-wrap {
			display: flex;
			align-items: center;
			
			&-stars {
				display: flex;
				align-items: center;
				margin-right: .9rem;

				&-star {
					width: 1.2rem;
					height: 1.2rem;
					flex-shrink: 0;
					cursor: pointer;

					&:not(:last-child) {
						margin-right: .4rem;
					}
				}
			}
		}

		&-number {
			font-size: 1.2rem;
			line-height: 117%;
		}
	}

	&__header {
		font-family: 'Museo Sans Cyrl 700';
		font-size: 7.6rem;
		line-height: 120%;
	}

	&__description {
		max-width: 52.8rem;
		margin-bottom: 4.1rem;
		font-size: 1.4rem;
		line-height: 150%;

		&-wrap {
			display: flex;
			align-items: center;
		}
	}

	&__button-play {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 13.2rem;
		height: 13.2rem;
		margin-left: 33.1rem;
		border-radius: 50%;
		background-color: #391917;
		cursor: pointer;
	}

	&__button-dark {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 10rem;
		height: 10rem;
		border-radius: 50%;
		background-color: #151515;

		& img {
			width: 3.5rem;
			height: 4rem;
		}
	}

	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 24.5rem;
		padding: 2.2rem 7.2rem;
		margin-bottom: 8.8rem;
		font-size: 1.4rem;
		line-height: 114%;
		color: #fff;
		text-decoration: none;
		background-color: #151515;
		border-radius: .8rem;
	}

	&-slider {
		
		&__wrap {
			max-width: 41.5rem;
			display: flex;
			align-items: center;
		}

		&__slide {
			position: relative;
			width: 16.4rem;
			height: 10.5rem;
			border-radius: .8rem;
			overflow: hidden;

			& img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			& span {
				position: absolute;
				left: 1.2rem;
				bottom: .6rem;
				transform: translate(0, 0);
				font-size: 1rem;
				font-weight: 120%;
			}
		}

		&__arrow {
			width: 3.2rem;
			height: 2.2rem;
			margin-left: 2.8rem;
			flex-shrink: 0;
			cursor: pointer;
		}
	}
}

.footer {
	height: 6rem;
	background: #030102;
	padding-top: 2rem;
	padding-bottom: 2rem;

	& .container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__copy {
		font-size: 1.4rem;
		line-height: 143%;
		color: #686767;
	}

	&__menu {
		display: flex;
		padding-left: 0;

		& li {
			list-style: none;

			&:not(:last-child) {
				margin-right: 2.5rem;
			}

			& a {
				color: #686767;
				text-decoration: none;
				font-size: 1.4rem;
				line-height: 143%;
			}
		}
	}
}

.mobile__menu {
	display: flex;
	background-color: #151515;
	box-sizing: border-box;
	position: fixed;
	right: -25rem;
	top: 0;
	z-index: 100;
	padding-top: 3.9rem;
	padding-right: 3.8rem;
	padding-bottom: 4rem;
	padding-left: 7.8rem;
	width: 25rem;
	height: 26.3rem;
	transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

	&-box {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		order: 1;
		margin-right: 2.5rem;
		
		&-logo {
			display: flex;
			width: 8rem;
			height: 2.9rem;
			margin-bottom: 3.5rem;
		}
		
		&-nav {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			order: 3;
			font-size: 1.4rem;
			line-height: 114%;

			& a:not(:last-child) {
				margin-bottom: 2rem;
			}
		}
		
		&-social {
			display: flex;
			justify-content: space-between;
			justify-content: center;
			order: 2;
			width: 9.3rem;
			margin-bottom: 2.2rem;
			
			&-icons {
				width: 1.7rem;
				height: 1.7rem;
				
				&:not(:last-child) {
					margin-right: 2rem;
				}
			}
		}
	}
	
	&-close {
		display: flex;
		order: 2;
		width: 2.3rem;
		height: 2.3rem;
	}
}

.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden;
	background-color: rgba($color: #000000, $alpha: 0.5);
	padding: 3rem 0;
	z-index: 100;
	display: none;

	&__inner {
		width: 100rem;
		height: 60rem;
	}
}

@media screen and (max-width: 1024px) {
	.container {
		width: 90rem;
	}

	.header__menu-nav {
		display: flex;
		margin-right: 45rem;
	}

	.main__button-play {
		margin-left: 20rem;
	}
}

@media screen and (max-width: 768px) {
    html {
        font-size: 12px;
    }
	
	.header {
		left: 0;
		top: 3rem;
		transform: translate(0, 0);
		
		&__menu {
			display: none;
		}
		
		&__menu-swicher {

			&-button {
				text-decoration: none;
				cursor: pointer;

				& img {
					width: 5rem;
				}
			}
		}
	}
	
	.main {
		background-position-x: 66%;
		padding-top: 12rem;

		&__menu-burger {
			display: block;
			position: absolute;
			top: 2%;
			right: 5%;
			width: 6rem;
			height: 6rem;
		}

		&__description {
			max-width: 40rem;
			margin-bottom: 2.1rem;
			font-size: 1.2rem;
			
			&-wrap {
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				margin-left: 0;
			}
		}

		&__button {
			margin-bottom: 4rem;

			&-play {
				width: 9.2rem;
				height: 9.2rem;
				margin-left: 6rem;
				margin-bottom: 4rem;
			}
	
			&-dark {
				   width: 7rem;
				height: 7rem;
	
				& img {
					width: 2.45rem;
					height: 2.8rem;
				}
			}
		}

		&-slider {

			&__wrap {
				max-width: 16.4rem;
			}

			&__arrow {
				display: none;
			}
		}


	}

	.container {
		width: 100%;
		padding-left: 3rem;
	}

	.footer {
		display: flex;
		height: initial;
		background-color: #141218;
		padding-top: 2.4rem;
		padding-bottom: 3.7rem;

		& .container {
			align-items: flex-start;
			flex-direction: column;
			padding-left: 3rem;
		}

		&__copy {
			padding-bottom: .8rem;
		}
	}
}

@media screen and (max-width: 685px) {

	.main {


		&__description {
			max-width: 40rem;
		}

		&__button {
			max-width: 20rem;
			padding: 1rem 5rem;
			margin-bottom: 3rem;
	
			&-play {
				width: 7.2rem;
				height: 7.2rem;
				margin-left: 4rem;
				margin-bottom: 2rem;
			}
	
			&-dark {
				width: 5.2rem;
				height: 5.2rem;
	
				& img {
					width: 2.45rem;
					height: 2.8rem;
				}
			}
		}
	}
}

@media screen and (max-width: 580px) {
	.main__description {
		max-width: 34rem;
	}

	html {
        font-size: 11px;
    }
}

@media screen and (max-width: 450px) {
	.main__description {
		max-width: 30rem;
	}

	html {
        font-size: 10px;
    }
}

@media screen and (max-width: 400px) {
	.main__description {
		max-width: 27.5rem;
	}

	html {
        font-size: 9px;
    }
}